import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'home',
        // component: Home
        components: {
            default: () => import(/* webpackChunkName: "home" */ '@/views/Home.vue'),
        },
        props: true
    },
    {
        path: '/Contact',
        name: 'Contact',
        components: {
            default: () => import('@/views/Contact.vue')
        }

    },
    {
        path: '/Join',
        name: 'Join',
        components: {
            default: () => import('@/views/Join.vue')
        },
        children:{
            path:'/music',
            name: 'music',
            components:{
                default:()=> import('@/views/Music')
            }

        }
    },
    {
        path: '/Mentions',
        name: 'Mentions',
        components: {
            default: () => import('@/views/Mentions.vue')
        }
    },


]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router